$easing: cubic-bezier(0.645, 0.045, 0.355, 1);

/* Fade */
.fade-enter {
  opacity: 0;
  transform: translateY(-20px);
  transition: all $easing 1s;
}
.fade-enter-active {
  opacity: 1 !important;
  transform: translateY(-10px);
  transition: all $easing 1s;
}
.fade-enter-done {
  opacity: 1 !important;
  transform: translateY(0);
  transition: all $easing 1s;
}
